
import { useEffect } from "react";

import BGM0 from "../../sfx/menu-music/AC7-title.wav";
import BGM1 from "../../sfx/menu-music/AC2-Hanger.mp3";
import BGM2 from "../../sfx/menu-music/AC2-Invoke.mp3";
import BGM3 from "../../sfx/menu-music/AC2-Lode Stone.mp3";
import BGM4 from "../../sfx/menu-music/AC3-briefing.mp3";
import BGM5 from "../../sfx/menu-music/AC3-linkage.mp3";
import BGM6 from "../../sfx/menu-music/AC4-Scramble.mp3";
import BGM7 from "../../sfx/menu-music/AC4-The Hangar.mp3";
import BGM8 from "../../sfx/menu-music/AC5-Hangar1.mp3";
import BGM9 from "../../sfx/menu-music/AC5-menu.mp3";
import BGM10 from "../../sfx/menu-music/AC5-Tutorial Main.mp3";
import BGM11 from "../../sfx/menu-music/AC6-menu.mp3";
import BGM12 from "../../sfx/menu-music/AC7-444th-Base.mp3";
import BGM13 from "../../sfx/menu-music/ACAH-hangar2.mp3";
import BGM14 from "../../sfx/menu-music/ACAH-refrain.mp3";
import BGM15 from "../../sfx/menu-music/ACX-aircraft_select.mp3";
import BGM16 from "../../sfx/menu-music/ACZ-Briefing1.mp3";
import BGM17 from "../../sfx/menu-music/ACZ-Hangar.mp3";
import BGM18 from "../../sfx/menu-music/ACZ-Sortie1.mp3";
import BGM19 from "../../sfx/menu-music/AC6-Sortie3.mp3";
import BGM20 from "../../sfx/menu-music/AC6-menu2.mp3";

const Music = ({ musicState }) => {
	const shuffle = (array) => {
		let currentIndex = array.length, randomIndex;
		// While there remain elements to shuffle...
		while (currentIndex !== 0) {
			// Pick a remaining element...
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex--;

			// And swap it with the current element.
			[array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
		}
		return array;
	}

	let trackList = [
		BGM0, BGM1, BGM2, BGM3, BGM4, BGM5, BGM6, BGM7, BGM8, BGM9, BGM10, 
		BGM11, BGM12, BGM13, BGM14, BGM15, BGM16, BGM17, BGM18, BGM19, BGM20
	];
	let currentTrack = 0;

	shuffle( trackList );

	let audio = new Audio(trackList[0]);
	audio.volume = 0.4;

	useEffect(() => {
		document.addEventListener("keydown", togglePlay);
		audio.addEventListener("ended", playNextSong);
		//playPauseMusic();
	}, [musicState]);

	const togglePlay = (event) => {
		if (event.keyCode === 118) {
			event.preventDefault();
			playPrevSong();
		} else if (event.keyCode === 119) {
			event.preventDefault();
			playPauseMusic();
		} else if (event.keyCode === 120) {
			event.preventDefault();
			playNextSong();
		}
	}

	const playPauseMusic = () => {
		musicState = !musicState;
		musicState ? audio.play() : audio.pause();
	}

	const playPrevSong = () => {
		if (currentTrack > 0) {
			currentTrack = currentTrack - 1;
		} else if (currentTrack === 0) {
			currentTrack = 19;
		}

		audio.src = trackList[currentTrack];
		audio.play();
	}

	const playNextSong = () => {
		if (currentTrack < 20) {
			currentTrack = currentTrack + 1;
		} else if (currentTrack === 20) {
			currentTrack = 0;
		}

		audio.src = trackList[currentTrack];
		audio.play();
	}

	return (
		<div onKeyPress={togglePlay}></div>
	)
}

export default Music
