
import './styles.css'

import Breadcumb from "../Breadcumb"
import SettingsLogo from "../../icons/cog.svg";

import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";
import { useState, useEffect } from "react"

const SettingsPage = () => {

    const [ pageFont, setPageFont ] = useState("7");
    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    useEffect(() => {
        let fontStored = JSON.parse(localStorage.getItem('font'));
        if ( fontStored ) {
            setPageFont( fontStored );
        }
    });


    const selectFontType = ( fontName ) => {
        let body = document.getElementsByTagName('body')[0];
        
        switch ( fontName ) {
            case '7':
                body.style.fontFamily = "AC7-Regular";
                break;

            case '6':
                body.style.fontFamily = "OCR-A";
                break;

            case '4':
                body.style.fontFamily = "ACES04";
                break;

            default:
                break;
        }

        localStorage.setItem('font', JSON.stringify(fontName));
        setPageFont( fontName );
        playSelect();
    }

    return (
        <div className="tw-flex tw-flex-row tw-h-screen tw-w-screen tw-justify-center">

            <div className="tw-flex tw-flex-col tw-w-3/5 tw-p-4">
                <Breadcumb secondLevel="Settings" iconSrc={ SettingsLogo }></Breadcumb>
                
                <p className="tw-text-2xl tw-text-left">
                    Plaform Font:<br /><br />
                </p>
                <ul>
                    
                    <li className={`tw-text-2xl ac7-font-item ac7-font-item ${pageFont === '7' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('7') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 7 - Skies Unknown
                    </li>

                    <li className={`tw-text-2xl ac7-font-item ac6-font-item ${pageFont === '6' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('6') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 6 - Fires of Liberation (OCR-A Regular)
                    </li>
                    
                    <li className={`tw-text-2xl ac7-font-item ac4-font-item ${pageFont === '4' ? 'active-list-item' : 'inactive-list-item'}`} onClick={ () => selectFontType('4') } onMouseEnter={ playNavigate }>
                        &gt; Ace Combat 4 - Shattered Skies
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default SettingsPage
