
import './styles.css';

import React from 'react';
import { useState, useEffect } from 'react';

import AppMarkdown from '../../changelog.md';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm'

import Breadcumb from "../Breadcumb";
import LogLogo from "../../icons/log.svg";

const Changelog = () => {
	
	const [ markdown, setMarkdown ] = useState('');

	useEffect(() => {
		// Get the contents from the Markdown file and put them in the React state, so we can reference it in render() below.
		fetch(AppMarkdown).then(res => res.text()).then(text => setMarkdown(text))
	});

	return (
		<div className="tw-flex tw-flex-row tw-h-screen tw-justify-center">
			<div className="tw-flex tw-flex-col tw-w-3/5 tw-p-4">
				<Breadcumb secondLevel="Changelog" iconSrc={LogLogo}></Breadcumb>

				<ReactMarkdown children={markdown} remarkPlugins={[remarkGfm]}/>	
      		</div>
    	</div>
  	);
};

export default Changelog;
