
import StatusLabel from "./StatusLabel"
import StatusDetail from "./StatusDetail"
import RadarChart from "./RadarChart"
import ReactCountryFlag from "react-country-flag"
import { useState, useEffect } from "react"

import Konami from 'react-konami-code';

const PlayerData = ({ player }) => {

    const [playerAvrg, setPlayerAvrg] = useState("--");
    const [playerFlag, setplayerFlag] = useState(null);
    const [playerCountry, setplayerCountry] = useState(null);
    const [playerRank, setPlayerRank] = useState("--");

    useEffect(() => {
        if ( player.statistics ) {
            let rnk = "--";

            //let mslAvr = missileCoefficientCalculation( player.statistics.mslAcc, player.statistics.mslCurving, player.statistics.mslLooping )
            //let mgAvr = machineGunCoefficientCalculation( player.statistics.mgAcc, player.statistics.mgRate );
            let average = rankingCoefficientCalculation (
                player.statistics.mslAcc, 
                player.statistics.mslCurving, 
                player.statistics.mslLooping, 
                player.statistics.mgAcc, 
                player.statistics.mgRate, 
                player.statistics.boost, 
                player.statistics.consistency, 
                player.statistics.strategy, 
                player.statistics.drift, 
                player.statistics.bkf
            );
            
            /*let average = rankingCoefficientCalculation(
                player.statistics.boost, 
                player.statistics.consistency, 
                mslAvr, 
                player.statistics.strategy, 
                mgAvr, 
                player.statistics.drift, 
                player.statistics.bkf);
            average = parseFloat( average );*/
            
            if ( average > 97 ) {
                rnk = "SS";

            } else if ( 97 >= average && average > 92 ) {
                rnk = "S1";
            } else if ( 92 >= average && average > 86 ) {
                rnk = "S2";
            } else if ( 86 >= average && average > 80 ) {
                rnk = "S3";

            } else if ( 80 >= average && average > 74 ) {
                rnk = "A1";
            } else if ( 74 >= average && average > 67 ) {
                rnk = "A2";
            } else if ( 67 >= average && average > 60 ) {
                rnk = "A3";

            } else if ( 60 >= average && average > 54 ) {
                rnk = "B1";
            } else if ( 54 >= average && average > 47 ) {
                rnk = "B2";
            } else if ( 47 >= average && average > 40 ) {
                rnk = "B3";

            }else if ( 40 >= average ) {

                rnk = "C";

            } else {
                rnk = "ER";
            }

            setPlayerAvrg( average.toFixed( 1 ) );
            setPlayerRank( rnk );
        }

        if ( player.country ) {
            setplayerFlag( <ReactCountryFlag countryCode={ player.country.code } style={{ fontSize: "2em" }} aria-label={ player.country.name }/> )
            setplayerCountry( <StatusDetail text={ player.country.name }/> );
        }
    },[player]);

    /*const missileCoefficientCalculation = ( mslAcc, mslCurving, mslLooping ) => {
        return mslAcc*0.7 + mslCurving*0.2 + mslLooping*0.1;
    }

    const machineGunCoefficientCalculation = ( mgAcc, mgRate ) => {
        return mgAcc*0.85 + mgRate*0.15;
    }
    
    const rankingCoefficientCalculation = (boost, chaining, mslCoff, strategy, mgCoff, drift, bkf ) => {
        return boost*0.2 + chaining*0.2 + mslCoff*0.15 + strategy*0.15 + mgCoff*0.15 + drift*0.1 + bkf*0.05;
    }*/

    // ranking calculator function
    const rankingCoefficientCalculation = ( mslAcc, mslCurving, mslLooping, mgAcc, mgRate, boost, chaining, strategy, drift, bkf ) => { 

        //series of weights for each stat
        let boostWeight = 0.2
        let chainingWeight = 0.2
        let mslAccWeight = 0.7
        let mslCurvingWeight = 0.2
        let mslLoopingWeight = 0.1  
        let mslWeight = 0.15
        let strategyWeight = 0.15
        let mgAccWeight = 0.85
        let mgRateWeight = 0.15
        let mgWeight = 0.15
        let driftWeight = 0.1
        let bkfWeight = 0.05

        // normalized ranking; ranking=100*playerRanking/maxRanking
        let ranking = 100*(boost*boostWeight + chaining*chainingWeight + 
            (mslAcc*mslAccWeight + mslCurving*mslCurvingWeight + mslLooping*mslLoopingWeight)*mslWeight + 
            strategy*strategyWeight + 
            (mgAcc*mgAccWeight + mgRate*mgRateWeight)*mgWeight + 
            drift*driftWeight + bkf*bkfWeight)
            /
            (100*boostWeight + 100*chainingWeight + 
                (100*mslAccWeight + 100*mslCurvingWeight + 100*mslLoopingWeight)*mslWeight + 
                100*strategyWeight + (100*mgAccWeight + 100*mgRateWeight)*mgWeight + 
                100*driftWeight + 100*bkfWeight
            );

        return ranking
    }

    const easterEgg = () => {
    alert('Hey, you typed the Konami Code! => ' + playerAvrg);
    }
    
    return (
        <div className="tw-flex tw-flex-col tw-overflow-auto lg:tw-overflow-visible tw-scrollbar-thin tw-scrollbar-thumb-blue-700 tw-scrollbar-track-blue-300">

            <div className="pilot-data tw-flex md:tw-flex-row tw-flex-col">
                <div className="pilot-picture tw-pb-2">
                    <img src={process.env.PUBLIC_URL + `${player.picture}`} alt="profile" width="453" height="255" />
                </div>

                <div className="md:tw-pl-4 tw-flex-grow">
                    <StatusLabel text="[NAME]" />
                    <StatusDetail text={ player.name }/>

                    <StatusLabel text="[CALLSIGN]" />
                    <StatusDetail text={ player.callsign }/>

                    <div className="tw-flex tw-flex-row">
                        <div className="tw-flex-grow">
                            <StatusLabel text="[MAIN PLANE]" />
                            <StatusDetail text={ player.mainPlane }/>
                        </div>
                        <div className="tw-flex-grow">
                            <StatusLabel text="[MAIN SPECIAL WEAPON]" />
                            <StatusDetail text={ player.mainSpW }/>
                        </div>
                    </div>

                    <div>
                        <StatusLabel text="[COUNTRY]" />
                        <div className="tw-flex tw-flex-row">
                            <div className="tw-pt-2 tw-pr-2">
                                { playerFlag }
                            </div>
                            { playerCountry }
                        </div>
                            
                    </div>
                    
                </div>
            </div>

            <div className="tw-pb-4">
                <StatusLabel text="[UNIT]" />
                <StatusDetail text={ player.unit }/>
            </div>

            <div className="pilot-data tw-flex xl:tw-flex-row tw-flex-col">
                <div className="tw-flex tw-flex-col tw-items-center">
                    
                    <div>
                        <img src={`images/badges/${ playerRank }.png`} alt={playerRank} height="64" width="64"></img>
                    </div>
                    <h1 className="tw-text-4xl md:tw-text-5xl tw-pb-6">RANK { playerRank }</h1>
                    <Konami action={easterEgg} timeout="3000">
                        {playerAvrg}
                    </Konami>

                </div>

                <div className="tw-flex tw-flex-col tw-flex-grow xl:tw-pl-8">
                    <div>
                        <label>[STATISTICS]</label>
                    </div>
                        
                    <RadarChart statistics={ player.statistics } average={ playerAvrg } />
                </div>
            </div>
        </div>

    )
}

export default PlayerData
